.wrapper {
    display: flex;     
    min-height: 100%;
    flex-direction: column;
    height: 100vh;
}

.nav-bar {
    padding: 10px;
    height: 50px;
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    /* background: #3399ff; */
}

.nav-item{
    padding: 20px;

}

.side-and-content{
    flex: 1 0 auto;
    display: flex;
    width: 100%;
}

.sidebar {
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.content-pane {
    padding: 20px;
    display: flex;
    flex-grow: 11;
    justify-content: center;
}

.footer {
    height: 50px;
    flex-shrink: 0;
    font-size: smaller;
    background: #3399ff;
    text-align: center;
    padding-top: 1em;
}

.show-details {
    display: flex;
    flex-direction: row;
    flex-grow: 11;
}

.meta{
    display: flex;
}

.meta-data{
    margin: 3px;
    flex-direction: column;
    justify-content: top;
}

.set-list {
    margin-top: 20px;
    display: flex;
    flex-grow: 11;
}

.field {
    color: white;
    box-shadow: 1px 1px 2px black;
    text-shadow: 1px 1px 2px #000000;
    padding: 5px;
    border-radius: 5px;
    margin: 20px;
    font-size: 16px;
    font-size: 2vw;
    background: #3399ff;
}

.set-title {
    color: white;
    box-shadow: 1px 1px 2px black;
    text-shadow: 1px 1px 2px #000000;
    margin: 3px;
    font-size: 16px;
    font-size: 2.5vw;
    border-radius: 5px;
    background: #3399ff;
}

.set-details {
    text-align: center;
    display: flex;
    flex-direction: column;
    flex-grow: 11;
}

.set-header {
    color: white;
    box-shadow: 1px 1px 2px black;
    text-shadow: 1px 1px 2px #000000;
    flex-direction: row;
    border-radius: 5px;
    background: #3399ff;
    display: flex;
    margin: 3px;
}

.set-header-field {
    color: white;
    text-shadow: 1px 1px 2px #000000;
    align-content: space-around;
    margin-bottom: 0;
    display: flex;
    padding: 5px;
    text-align: left;
    flex: 5;
}

.set-header-field-length {
    color: white;
    text-shadow: 1px 1px 2px #000000;
    align-content: space-around;
    margin-bottom: 0;
    display: flex;
    padding: 5px;
    justify-content: flex-end;    
    flex: 5;

}

.list-flex {
    display:flex
}
.track-info {
    
    display: flex;
    flex-direction: row;
    flex-grow: 11;
}

.track-info-pos {
    /* box-shadow: 1px 1px 2px black; */
    width: 25px;    
    text-align: center;
    margin: 3px;
    padding: 3px;
    flex: 5% 11;

    /* background: #eaecee;
    border-radius: 5px; */

}

.track-info-name-length {
    /* box-shadow: 1px 1px 2px black; */
    display: flex;
    flex: 11;
    margin: 3px;
    padding: 3px;
    /* background: #eaecee;
    border-radius: 5px; */
}

.track-info-title {
    display: flex;
    flex: 11;
    margin: 3px;
    padding: 3px;
    justify-content: flex-start;    
    flex: 95% 11;
    

}

.track-info-length {
    display: flex;
    flex: 11;
    margin: 3px;
    padding: 3px;
    justify-content: flex-end;    
    flex: 5;

}

/* .year-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
} */

.list-group {
    padding: 0;
}

.show-row-1 {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.show-row-2 {
    justify-content: center;
}

.show-field {
    padding: 10px;
    
}

.taper-notesOFF {
    display: none;
}

.taper-notesON {
    display: flex;
    text-align: left;
}

 .nav-link.list{
    
    padding: 0;
    margin: 0;
    overflow-x: scroll
    
}

.btn.btn-primary {
    text-align: center;
    border: 0;
    padding: 0;
    margin: 0;
}
.btn.btn-primary.shows {
    text-align: left;
    border: 0;
    padding: 0;
    margin: 0;
}   

.container {
max-height: 600px;
overflow-x: scroll}  

.text-box {
    text-justify: auto;
    margin: 20px;
}